.wrapper__contact{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 100px;
  h1{
    padding: 50px 0;
    font-size: 34px;
    font-weight: 500;
  }
  .contact__item{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    button{
      background-color: black;
      color: white;
      padding: 15px;
      cursor: pointer;
      border-radius: 50px;
      font-size: 18px;
      margin-top: 30px;

      &:active{
        font-size: 18.2px;
      }
    }
  }

  .contact__links{
    margin-top: 30px;
    font-size: 35px;

    a{
      color: black;
    }
  }
}